<template>
  <div id="detail">
    <van-overlay :show="load" :z-index="10000" class="loadBox" :lock-scroll="true">
      <van-loading class="load" type="spinner" color="#1989fa" :vertical="true" v-if="load" />
    </van-overlay>
    <div class="content">
      <div class="imgBox">
        <van-swipe class="my-swipe" :autoplay="3000">
          <van-swipe-item v-for="(image, index) in data.acImages" :key="index">
            <img :src="image" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="videoBaseInfo">
        <div class="videoTitle">
          <p class="videoName">{{data.name}}</p>
          <p class="videoSort">
            <span v-for="(item,index) in data.tags" :key="index">
              <span v-if="index!==0">/</span>
              {{item}}
            </span>
          </p>
        </div>
        <div class="videoBrief">
          <div :class="{shrink:showToggleBtn&&showVideoBrief}">
            <div class="videoBriefText" ref="videoBriefText" v-html="introduction"></div>
            <div v-if="showToggleBtn&&showVideoBrief" class="ellipsis">...</div>
          </div>
          <p
            class="changeBrief"
            v-if="showToggleBtn"
            @click="changeBrief"
          >{{showVideoBrief?"展开":"收起"}}</p>
        </div>
        <div class="videoCom">
          <div class="comLogo">
            <img :src="data.logo" alt />
          </div>
          <p>{{data.studio_name}}</p>
        </div>
        <div class="videoDetail" v-html="data.intro"></div>
        <div class="videoBuy">
          <div class="toUser" @click="toUser">
            <i class="iconfont icon-wode"></i>
            <span>个人中心</span>
          </div>
          <div class="buyBtn" @click="WXPay">
            抢购价￥
            <span>{{data.price|moneyFormatType}}</span>
          </div>
        </div>
        <div class="shareBtn" @click="toShare">
          分享赚￥
          <span>{{data.price*data.rate/100|moneyFormatType}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getWX,
  getDetailData,
  getWXPay,
  getWXConfig,
} from "../assets/javascript/apis.js";

export default {
  data() {
    return {
      introduction: "",
      showToggleBtn: false,
      showVideoBrief: true,
      load: false,
      getDataFlag: true,
      data: {},
    };
  },
  created() {
    let ac_id = this.getUrlParam("ac_id"),
      from_user_id = this.getUrlParam("from_user_id");
    console.log(ac_id);
    console.log(from_user_id);
    this.$store.commit("setDistributionInfo", {
      ac_id,
      from_user_id,
    });
    //如果没有获得到对应的数据，则跳转向错误页面
    // if (
    //   this.$store.state.distributionInfo.ac_id &&
    //   this.$store.state.distributionInfo.from_user_id
    // ) {
    this.getData();
    // } else {
    //   this.$router.push("/errorPage");
    // }
  },
  methods: {
    //获取地址栏参数,没有对应的参数则返回false
    getUrlParam(name) {
      let urlParam = window.location.search.substr(1).split("&");
      for (let i = 0; i < urlParam.length; i++) {
        if (urlParam[i].split("=")[0] == name) {
          return urlParam[i].split("=")[1];
        }
      }
      return false;
    },
    //获取页面数据
    getData() {
      this.load = true;

      if (!this.getDataFlag) return;
      this.getDataFlag = false;

      const data = {
        // ac_id: this.$store.state.distributionInfo.ac_id,
        // user_id: this.$store.state.WXUserInfo.id
        //   ? this.$store.state.WXUserInfo.id
        //   : "",
        ac_id: 1,
        user_id: 48,
      };

      getDetailData(data).then((res) => {
        console.log(res);
        this.data = res;
        this.introduction = res.introduction;
        this.load = false;
        this.getDataFlag = true;
      });
    },
    //购买视频
    WXPay() {
      // debugger;
      if (
        this.$store.state.WXUserInfo.id &&
        this.$store.state.appUserInfo.token
      ) {
        //获取config验证消息
        const data = {
          jsapis: "chooseWXPay",
          url: window.location.href.replace(window.location.hash, ""),
        };
        getWXConfig(data).then((res) => {
          // debugger;
          console.log(res);
          //将微信config验证的信息，更新到vuex中
          this.$store.commit("setWXConfig", res);
          const data = {
            // ac_id: this.$store.state.distributionInfo.ac_id, //要修改
            ac_id: 1, //要修改
            from_user_id: this.$store.state.WXUserInfo.id, //要修改
            user_id: this.$store.state.WXUserInfo.id,
            openid: this.$store.state.WXUserInfo.open_id,
          };

          //获取发起支付请求所需的信息
          getWXPay(data).then((res) => {
            console.log(res);
            //发起支付请求
            this.$payConfig(
              res.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
              res.nonceStr, // 支付签名随机串，不长于 32 位
              res.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
              res.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
              res.paySign, // 支付签名
              function () {
                console.log("支付成功");
              }
            );
          });
        });
      } else {
        this.$router.push("/login");
      }
      //注释信息
      {
        // const data = {
        //   "ac_id=": 1, //要修改
        //   "from_user_id=": this.$store.state.WXUserInfo.id, //要修改
        //   "user_id=": this.$store.state.WXUserInfo.id,
        //   "openid=": this.$store.state.WXUserInfo.open_id,
        // };
        // console.log(data);
        // buyVideo(data).then((res) => {
        //   console.log(res);
        //   //根据返回信息，调起支付接口
        //   this.$payConfig(
        //     res.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
        //     res.nonceStr, // 支付签名随机串，不长于 32 位
        //     res.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
        //     res.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
        //     res.paySign // 支付签名
        //   );
        // });
        // if (
        //   this.$store.state.WXUserInfo.id &&
        //   this.$store.state.appUserInfo.token
        // ) {
        //   let url =
        //     "https://app.newtiming.com/api/v1/activity/wechat/pay/buy/program?" +
        //     "ac_id=" +
        //     1 + //要修改
        //     "&from_user_id=" +
        //     this.$store.state.WXUserInfo.id + //要修改
        //     "&user_id=" +
        //     this.$store.state.WXUserInfo.id +
        //     "&openid=" +
        //     this.$store.state.WXUserInfo.open_id;
        //   window.location.href = url;
        // } else {
        //   this.$router.push("/login");
        // }
      }
    },
    //去往个人中心页面
    toUser() {
      this.$router.push({ name: "personalCenter" });
    },
    //去往分享页面
    toShare() {
      this.$router.push({ name: "share" });
    },
    //改变视频简介信息状态
    changeBrief() {
      this.showVideoBrief = !this.showVideoBrief;
    },
  },
  watch: {
    introduction: function () {
      this.$nextTick(function () {
        let videoBriefHeight = parseFloat(
          window.getComputedStyle(this.$refs.videoBriefText).height
        );
        let briefLineHeight = parseFloat(
          window.getComputedStyle(this.$refs.videoBriefText).lineHeight
        );
        console.log(videoBriefHeight, briefLineHeight);
        if (videoBriefHeight > briefLineHeight * 3) {
          console.log(1);
          this.showToggleBtn = true;
        } else {
          console.log(2);
          this.showToggleBtn = false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#detail {
  height: 100%;
  width: 100%;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .imgBox {
      width: 100%;
      height: 221px;
      /deep/.my-swipe {
        width: 100%;
        height: 221px;
        .van-swipe__track {
          .van-swipe-item {
            img {
              width: 100%;
              height: 221px;
              background: red;
            }
          }
        }
        .van-swipe__indicators {
          .van-swipe__indicator {
            background: linear-gradient(
              -10deg,
              rgba(76, 170, 241, 1),
              rgba(155, 120, 240, 1)
            );
            opacity: 0.7;
          }
          .van-swipe__indicator--active {
            background: rgb(39, 11, 104);
          }
        }
      }
    }
    .videoBaseInfo {
      flex-grow: 1;
      margin-top: 20px;
      padding: 0 15px;
      background: white;
      display: flex;
      flex-direction: column;
      .videoTitle {
        height: 40px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        p {
          word-break: break-all;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .videoName {
          font-size: 15px;
          font-weight: 500;
          color: rgba(7, 7, 7, 1);
        }
        .videoSort {
          font-size: 12px;
          color: rgba(170, 170, 170, 1);
        }
      }
      .videoBrief {
        width: 100%;
        margin-top: 20px;
        font-size: 12px;
        color: rgba(85, 85, 85, 1);
        .shrink {
          position: relative;
          max-height: 63px;
          overflow: hidden;
          .ellipsis {
            position: absolute;
            bottom: 2px;
            right: 10px;
            line-height: 21px;
            font-size: 20px;
            padding: 0 0 0 20px;
            background: red;
            background: -webkit-linear-gradient(left, transparent, #fff 55%);
            background: -o-linear-gradient(right, transparent, #fff 55%);
            background: -moz-linear-gradient(right, transparent, #fff 55%);
            background: linear-gradient(to right, transparent, #fff 55%);
          }
        }
        .videoBriefText {
          height: auto;
          line-height: 21px;
          word-wrap: break-word;
          word-break: break-all;
        }
        .changeBrief {
          text-align: right;
          padding: 0 10px;
          font-size: 12px;
          color: rgba(75, 165, 245, 1);
          cursor: pointer;
        }
      }
      .videoCom {
        width: 100%;
        height: 35px;
        margin-top: 25px;
        background: rgba(242, 244, 246, 1);
        display: flex;
        justify-content: center;
        align-items: center;
        .comLogo {
          width: 30px;
          height: 30px;
          margin-right: 10px;
          img {
            width: 30px;
            height: 30px;
          }
        }
        p {
          font-size: 12px;
          font-weight: 500;
          color: rgba(85, 85, 85, 1);
        }
      }
      .videoDetail {
        width: 100%;
        flex-grow: 2;
        margin: 15px 0 70px 0;
        background: rgba(238, 238, 238, 1);
      }
      .videoBuy {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 60px;
        box-sizing: border-box;
        padding: 10px 15px;
        background: white;
        display: flex;
        align-items: center;
        .toUser {
          width: 60px;
          height: 100%;
          background: white;
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          align-items: center;
          i {
            font-size: 24px;
          }
          span {
            font-size: 9px;
            font-weight: 500;
            color: rgba(85, 85, 85, 1);
          }
        }
        .buyBtn {
          flex-grow: 1;
          height: 100%;
          background: linear-gradient(
            -10deg,
            rgba(76, 170, 241, 1),
            rgba(155, 120, 240, 1)
          );
          border-radius: 8px;
          font-size: 15px;
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          line-height: 40px;
          text-align: center;
        }
      }
      .shareBtn {
        position: fixed;
        top: 250px;
        right: 0;
        height: 30px;
        max-width: 100px;
        padding: 0 10px;
        line-height: 30px;
        text-align: center;
        background: linear-gradient(
          -10deg,
          rgba(76, 170, 241, 1),
          rgba(155, 120, 240, 1)
        );
        border-radius: 25px 0px 0px 25px;
        font-size: 11px;
        color: rgba(255, 255, 255, 1);
        word-break: break-all;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}
</style>